<template>
  <div class="shoppingCart">
    <van-nav-bar title="购物车" left-text="返回" left-arrow @click-left="goBack" />
    <!-- 图片 -->
    <div v-if="!good_id">
      <van-empty
        description="购物车什么也没有......"
        image="https://timgsa.baidu.com/timg?image&quality=80&size=b9999_10000&sec=1607095361741&di=45b4a1793f5a6d6f7ac6802fb022a0cd&imgtype=0&src=http%3A%2F%2Fwww.kangjiayiyao.com%2Fimages%2Fempty.gif"
        image-size="200"
      >
        <van-button round color="#c43b31" class="bottom-button" @click="goSameCity">
          先逛一逛
        </van-button>
      </van-empty>
    </div>
    <div class="content-warpper" v-if="good_id">
      <!-- 安全购物环境 -->
      <div class="title-box clearfix">
        <div class="fl img-box">
          <img class="auto-img" src="../assets/images/security.png" alt="" />
        </div>
        <div class="title-tips fl">你正在安全购物环境中，请放心购买</div>
      </div>
      <!-- 地址 -->
      <div @click="openAddress" class="address-box clearfix">
        <div class="user-info fl" v-if="!Show">
          <div class="user-address-fake fl">选择收货地址 <span>＞</span></div>
        </div>
        <div class="user-info fl" v-if="Show">
          <div class="user-box clearfix">
            <div class="user-name fl">{{ addressInfo.name }}</div>
            <div class="user-phone fl">{{ addressInfo.tel }}</div>
          </div>
          <div class="user-address">
            {{
              `${addressInfo.city}${addressInfo.province}${addressInfo.county}${addressInfo.addressDetail}`
            }}
          </div>
        </div>
        <div class="fl address-arrow" v-if="Show">＞</div>
      </div>
      <!-- 弹出地址选择 -->
      <van-popup
        v-model="isShow"
        position="bottom"
        :style="{ height: '60%' }"
        closeable
        round
      >
        <div class="addAddress">添加收货地址</div>
        <van-address-edit
          :area-list="areaList"
          show-search-result
          :address-info="addressInfo"
          save-button-text="保存地址"
          @save="save"
        />
      </van-popup>
      <!-- 商品信息 -->
      <div class="produtc-information-box">
        <div class="product-title">詠槑旗舰店</div>
        <div class="information-box clearfix">
          <div class="fl produtc-img-box">
            <img class="auto-img" :src="ProductDetailData.goods_small_logo ? ProductDetailData.goods_small_logo : 'http://image1.suning.cn/uimg/b2c/newcatentries/0070067836-000000010000163963_2_400x400.jpg'" alt="" />
          </div>
          <div class="fl product-content">
            <div class="clearfix product-little-box">
              <div class="fl product-name tow-text">
                {{ ProductDetailData.goods_name }}
              </div>
              <div class="fl product-price-box">
                <div class="product-price">￥ {{ ProductDetailData.goods_price }}</div>
                <div class="product-fake-price">￥ {{ ProductDetailData.fakePrice }}</div>
              </div>
            </div>
            <div class="clearfix stepper-box">
              <div class="fl">
                <van-stepper v-model="value" />
              </div>
              <div @click="isDelete" class="fl close-icon">x</div>
            </div>
          </div>
        </div>
      </div>
      <!-- 最终价格 -->
      <div class="product-total-price">
        总计：￥{{ ProductDetailData.goods_price * value }}
      </div>
      <!-- 确认支付 -->
      <div class="confirm-pay">
        <div class="total-price-box">
          还需支付:
          <span class="total-price">￥{{ ProductDetailData.goods_price * value }}</span>
        </div>
        <div class="go-pay" @click="goPay">在线支付</div>
      </div>
      <!-- 提示是否删除订单信息 -->
      <van-cell>
        <van-popup v-model="showcontent">
          <div class="delete-box">
            <div class="delete-title">删掉的商品可能被别人抢走哦，要删除吗？</div>
            <div class="isdelete clearfix">
              <div @click="isDelete" class="Ydelete fl">再想想</div>
              <div @click="deleteShowcart" class="Ndelete fl">删除</div>
            </div>
          </div>
        </van-popup>
      </van-cell>
    </div>
  </div>
</template>

<script>
import "../assets/less/live.less";
import areaList from "../assets/js/area";
export default {
  name: "Live",
  data() {
    return {
      good_id: "",
      value: 1,
      isShow: false,
      Show: false,
      areaList,
      // 地址初始值
      addressInfo: {},
      // 商品详情数据
      ProductDetailData: "",
      showcontent: false,
      isShowShopcart:false
    };
  },
  created() {
    // 截取goods_id
    this.good_id = this.$route.params.goods_id;
    // 获取商品详情数据
    this.getProductData();
    this.value = localStorage.getItem('myCount')
  },
  methods: {
    goSameCity() {
      this.$router.push({
        name: "SameCity",
      });
    },
    goBack() {
      this.$router.go(-1);
    },
    // 获取商品信息
    getProductData() {
      this.axios({
        method: "GET",
        url: "https://api-hmugo-web.itheima.net/api/public/v1/goods/detail",
        params: {
          goods_id: this.good_id,
        },
      })
        .then((result) => {
          this.$toast.clear();
          this.ProductDetailData = result.data.message;
          this.ProductDetailData.fakePrice =
            result.data.message.goods_price * Math.ceil(Math.random() * 9);
        })
        .catch((err) => {
          this.$toast.clear();
        });
    },
    // 选中地址
    openAddress() {
      this.isShow = true;
    },
    //保存地址
    save(content) {
      //复制content
      let addressInfo = Object.assign({}, content);
      //删除country
      delete addressInfo.country;
      this.addressInfo = addressInfo;
      this.Show = true;
      this.isShow = false;
    },
    // 在线支付
    goPay() {
      if (!this.Show) {
        return this.$toast("请选择收货地址");
      }
      this.$toast("支付成功");
      localStorage.removeItem('myCount');
      this.good_id = false
    },
    isDelete() {
      this.showcontent = !this.showcontent;
    },
    // 删除购物车
    deleteShowcart() {
      this.good_id = false;
      this.$toast("删除成功");
    },
  },
};
</script>
